import React from "react";
import styled from "styled-components";

const Atag = styled.a`
  cursor: pointer;
  text-decoration: underline;
  &:hover {
    transition: 0.5s ease-in-out;
    font-weight: 400;
    color: ${(props) =>
      props.bwColor === "white" ? "rgba(255,255,255,1)" : "rgba(0,0,0,1)"};
  }
`;

const Btag = styled.span`
  font-weight: 400;
  display: flex;
  margin-top: 10px;
  margin-bottom: 10px;
`;

const Block = styled.div`
  display: flex;
  width: 100%;
  height: 10px;
`;

const Minus = styled.div`
  font-size: 16px;
  color: ${(props) =>
    props.bwColor === "white" ? "rgba(255,255,255,0.05)" : "rgba(0,0,0,0.1)"};
  margin-top: 8px;
  margin-bottom: 14px;
`;

const OL = ({ url, children, bwColor, target = "_blank" }) => {
  return (
    <Atag target={target} href={url} bwColor={bwColor}>
      {children}
    </Atag>
  );
};

const BT = ({ children }) => {
  return <Btag>{children}</Btag>;
};

const BL = () => {
  return <Block></Block>;
};

const HB = ({ bwColor }) => {
  return (
    <Minus className="dD" bwColor={bwColor}>
      -
    </Minus>
  );
};

export { OL, BT, BL, HB };
